* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(33, 33, 33, .557);
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

p {
  margin: 0;
}

b {
  font-size: 14px;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.search-form {
  background-color: #6495ed;
  border-radius: 2px;
  padding: 5px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.header-search {
  padding: 10px;
}

.input-search {
  width: 170px;
  height: 22px;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 2px;
}

.input-search:focus {
  border-radius: 2px;
  outline: 2px solid #3373ea;
}

.photo-card {
  margin-bottom: 10px;
  margin-right: 10px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.thumb {
  width: 240px;
  height: 160px;
}

.button-search {
  color: #fff;
  background-color: #6495ed;
  border: 2px solid #6495ed;
  margin-left: 5px;
}

.load-more {
  color: #fff;
  background-color: #6495ed;
  border: 2px solid #6495ed;
  border-radius: 2px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

